.container {
  display: flex;
  justify-content: left;
  
}

.icon {
  text-align: center;
  margin-right: 100px;
  margin-bottom: 20px;
  overflow: hidden;
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}
.icon:hover {
  transform: scale(1.01);
}