@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono');
.header {
  font-family: 'Roboto Mono';
  font-size: 20px;
  background-color: #333; 
  color: #fff; 
  padding: 10px; 
}

.nav {
  font-family: 'Roboto Mono';
  display: flex;
  justify-content: space-around;
}

.item {
  font-family: 'Roboto Mono';
  text-decoration: none;
  color: #fff;
  padding: 10px 20px;
  transition: background-color 0.3s;
}

.item:hover {
  background-color: #555;
}

.left {
  display: flex;
  align-items: center;
}

.logo {
  font-size: 30px;
  font-weight: bold;
}

.contents {
  padding-left: 50px;
}

.container {
  max-width: 100%;
  max-height: 400px;
  margin-right: 20px;
  overflow: hidden;
  border-radius: 8px;
}

.image-row {
  display: flex;
  justify-content: flex-start;
}

.image-container {
  max-width: 100%;
  max-height: 400px;
  margin-right: 20px;
  overflow: hidden;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.image-container:hover {
  transform: scale(1.01);
}